<template>
  <div class="login-container">
    <van-nav-bar title="我的信息"> </van-nav-bar>
    <!-- <van-progress :percentage="50" stroke-width="8" /> -->
    <img class="round_icon" src="@/assets/logo.png" alt="" />
    <van-tabs v-model="active" @click="onClickWarning">
      <van-tab title="报警记录">
        <van-cell
          v-for="item in msgList"
          :key="item.id"
          :value="item.oxygen_limit"
          :label="item.date_time"
          title="报警记录："
        >
          <div>
            <h5>氧限：{{item.oxygen_limit}}</h5>
            <h5>溶氧值：{{item.oxygen_data}}</h5>
            <h5>温度：{{item.temperature}}℃</h5>
          </div>
        </van-cell>
      </van-tab>
      <van-tab title="氧限操作">
        <van-cell
          v-for="item in limitList"
          :key="item.id"
          :label="item.date_time"
          title="氧限操作记录："
        >
          <div>
            <h5>氧限：{{item.oxygen_limit}}</h5>
            <h5>操作终端：{{item.device_info}}</h5>
          </div>
        </van-cell>
      </van-tab>
    </van-tabs>
    <!-- <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
    </van-list> -->
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { Toast } from "vant";
import { getLimitOxygenRecord, getWarningOxygenRecord } from "@/api/index";
export default {
  name: "ToolControl",
  data() {
    return {
      active: 0,
      msgList: [],
      limitList: [],
      finished: false,
      loading: false,
    };
  },
  watch: {
    $route: {
      handler: function (route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true,
    },
  },
  mounted(){
    this.onClickWarning("", "报警记录")
  },
  methods: {
    onLoad() {
      this.isLoading = true;
      new Promise((resolve, reject) => {
        getLimitOxygenRecord()
          .then((response) => {
            const { data } = response;
            console.log("data:", data);
            this.msgList = data;
            this.loading = true;
            this.finished = true;
          })
          .catch((err) => {
            Toast.fail("后台服务异常！");
            console.log("err_test:", err);
            this.isLoading = false;
          });
      });
    },
    onClickWarning(name, title) {
      this.isLoading = true;
      let getRecordFunc = title==="报警记录"? getWarningOxygenRecord : getLimitOxygenRecord
      new Promise((resolve, reject) => {
        getRecordFunc()
          .then((response) => {
            const { data } = response;
            console.log("data:", data);
            if(title=="报警记录"){
              this.msgList = data;
            }else{
              this.limitList = data;
            }
            
            this.loading = true;
            this.finished = true;
          })
          .catch((err) => {
            Toast.fail("后台服务异常！");
            console.log("err_test:", err);
            this.isLoading = false;
          });
      });
    },
  },
};
</script>

<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;

.sliderval {
  position: fixed;
  top: 30%;
  width: 100%;
  // height: 50%;
}

.login-container {
  min-height: 100%;
  width: 100%;
  // background-color: $bg;
  overflow: hidden;
  .round_icon {
    width: 34px;
    height: 34px;
    display: flex;
    border-radius: 50%;
    // align-items: center;
    // justify-content: center;
    overflow: hidden;
  }
}
</style>
